<template>
	<div>
		<div class="personInfor">
			<div class="avatar">

			</div>
			<div class="text">
				<!--	<p style="color: #000000;">周晓晓</p>-->
				<!--<p style="color: #000000;">人力资源A班</p>-->
			</div>
		</div>
		<div class="item" @click="goEach('学习公告')">
			<i></i>
			<span>学习公告</span>
			<span style="color: #fa8a12;float: right;margin-right: 0.3rem;">{{res.read}}</span>
		</div>
		<div class="item" @click="goEach('重点巩固')">
			<i></i>
			<span>重点巩固</span>
			<!--	<span style="color: #fa8a12;float: right;margin-right: 0.3rem;">2</span>-->
		</div>
		<div class="item" @click="goEach('每日一练')">
			<i></i>
			<span>每日一练</span>
			<!--<span style="color: #fa8a12;float: right;margin-right: 0.3rem;">2</span>-->
		</div>
		<div class="item" @click="goEach('通关练习')">
			<i></i>
			<span>通关练习</span>
			<span style="color: #fa8a12;float: right;margin-right: 0.3rem;">{{res.schedule}}</span>
		</div>
		<div class="item" @click="goEach('在线考试')">
			<i></i>
			<span>在线考试</span>
			<span v-if="res.examPaper==1" style="float: right;margin-right: 0.3rem;">进行中</span>

		</div>
		<div class="item" @click="goEach('错题集')">
			<i></i>
			<span>错题集</span>

		</div>
		<div v-if="res.secret==1" class="item" @click="showError">
			<i></i>
			<span>冲刺密卷</span>
			<span style="color: #999999;float: right;margin-right: 0.3rem;">未开通</span>

		</div>
		<div v-if="res.secret==2" class="item" @click="goEach('冲刺密卷')">
			<i></i>
			<span>冲刺密卷</span>

			<span style="float: right;margin-right: 0.3rem;">已开通</span>
		</div>

		<div class="item" @click="goEach('成绩排行')">
			<i></i>
			<span>成绩排行</span>
			<!--<span style="color: #fa8a12;float: right;margin-right: 0.3rem;">2</span>-->
		</div>
		<!--@click="goEach('评价')"-->
		<div class="item" @click="showError">
			<i></i>
			<span>评价</span>
			<span style="color: #999999;float: right;margin-right: 0.3rem;">未开通</span>
			<!--<span style="color: #fa8a12;float: right;margin-right: 0.3rem;">2</span>-->
		</div>
		<Bottom></Bottom>
	</div>
</template>

<script>
	import Bottom from '../../components/Bottom/Bottom'
	export default {
		components: {
			Bottom
		},
		data() {
			return {
				res: {}
			}
		},
		created() {

			this.$Api.StudyField(this.$store.state.sessionId).then((res) => {
				console.log(res)
				if(res.q.d!="操作成功"){
					this.$router.push({
						
                    name:"Binding"
						
					})
				}else{
					this.res = res.q.study
				}
				

			})

		},
		methods: {
			showError() {

				this.$toast.center('暂未开通此服务');
			},
			goEach(text) {
				if(text == '成绩排行') {
					this.$router.push({
						name: 'Rank'
					})
				} else if(text == "冲刺密卷") {
					this.$router.push({
						name: 'Examination',
						params: {
							action: 2
						}
					})
				} else if(text == '错题集') {
					this.$router.push({
						name: 'Error'
					})

				} else if(text == "评价") {
					this.$router.push({
						name: 'Evaluate'
					})
				} else if(text == "学习公告") {
					this.$router.push({
						name: 'Notice'
					})
				} else if(text == "重点巩固") {
					this.$router.push({
						name: 'Consolidate',
						params: {
							type: "留言"
						}
					})
				} else if(text == "每日一练") {
					this.$router.push({
						name: 'Practice'
					})
				} else if(text == "通关练习") {
					this.$router.push({
						name: 'Consolidate',
						params: {
							type: "练习"
						}
					})
				} else if(text == "在线考试") {
					this.$router.push({
						name: 'Examination',
						params: {
							action: 1
						}
					})
				}

			}
		}

	}
</script>

<style scoped="scoped" lang="scss">
	@import "../../sass/baseSass.scss";
	.personInfor {
		width: 100%;
		height: 2.5rem;
		background: url(../../assets/background.png) no-repeat;
		background-size: 100% 100%;
		padding: 0.45rem 0.3rem;
		.avatar {
			width: 1.5rem;
			height: 1.5rem;
			background: url(https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1522300298011&di=e4b387e3c04d878c58d6cb54385a73a6&imgtype=0&src=http%3A%2F%2Fwww.qqzhi.com%2Fuploadpic%2F2014-09-23%2F000247589.jpg) no-repeat;
			background-size: 100% 100%;
			border-radius: 50%;
			float: left;
		}
		.text {
			float: left;
			padding: 0.1rem 0.3rem;
			box-sizing: border-box;
			color: #FFFFFF;
			line-height: 0.6rem;
			font-size: 0.32rem;
		}
	}
	
	.item {
		width: 100%;
		height: 0.9rem;
		border-bottom: 0.02rem solid #CCCCCC;
		position: relative;
		line-height: 0.9rem;
		font-size: 0.3rem;
		padding: 0 0.3rem;
		box-sizing: border-box;
		i {
			width: 0.13rem;
			height: 0.23rem;
			background: url(../../assets/go.png) no-repeat;
			background-size: 100% 100%;
			position: absolute;
			right: 0.3rem;
			top: 38%;
		}
	}
</style>


